/* caveat-brush-regular - latin */
@font-face {
  font-family: 'Caveat Brush';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/caveat-brush-v6-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('../fonts/caveat-brush-v6-latin-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/caveat-brush-v6-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../fonts/caveat-brush-v6-latin-regular.woff') format('woff'),
    /* Modern Browsers */ url('../fonts/caveat-brush-v6-latin-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('../fonts/caveat-brush-v6-latin-regular.svg#CaveatBrush')
      format('svg'); /* Legacy iOS */
}
